<template>
  <div class="container mt-4">
    <div v-if="loading" class="text-center">
      <div class="spinner-border" role="status">
        <span class="visually-hidden">Chargement...</span>
      </div>
    </div>
    <div v-if="error" class="alert alert-danger">{{ error }}</div>
    <div v-if="catalog">
      <div class="d-flex justify-content-between align-items-center mb-3">
        <h1>{{ catalog.title }}</h1>
        <button class="btn btn-outline-primary" @click="toggleNoteMode">
          {{ isNoteMode ? 'Quitter le mode Notes' : 'Activer le mode Notes' }}
        </button>
      </div>
      <hr />
      <h2>Pages</h2>
      <div class="catalog-container">
        <div v-for="(pair, index) in pairedPages" :key="index" class="page-pair">
          <!-- Page de gauche -->
          <div class="page left" v-if="pair[0]" @click="startNewNote($event, pair[0])">
            <div class="card-body">
              <img v-if="pair[0].active_image_version && pair[0].active_image_version.fichier_jpeg"
                   :src="getStrapiMediaUrl(pair[0].active_image_version.fichier_jpeg.url)"
                   alt="Version Image"
                   class="version-image">
              <div class="products-grid">
                <div v-for="product in pair[0].products" :key="product.id" class="product-item">
                  <img v-if="product.image"
                       :src="getStrapiMediaUrl(product.image.url)"
                       :alt="product.name"
                       class="product-image">
                  <strong>{{ product.name }}</strong>
                  <span>${{ product.price }}</span>
                  <p>{{ product.description }}</p>
                </div>
              </div>
            </div>
            <template v-if="isNoteMode">
              <div 
                v-for="note in pair[0].notes" 
                :key="note.documentId" 
                class="note-bubble" 
                :class="{ 'is-dragging': draggedNote && draggedNote.note.documentId === note.documentId }"
                :style="{ top: note.y + '%', left: note.x + '%' }"
                @mousedown.stop="startDrag($event, note)"
              >
                <button @click.stop="deleteNote(note)" class="delete-note-btn">×</button>
                <a v-if="note.attachment" :href="getStrapiMediaUrl(note.attachment.url)" target="_blank" class="me-2 paperclip-link">
                  <i class="bi bi-paperclip"></i>
                </a>
                <p>{{ note.content }}</p>
              </div>
              <div v-if="newNoteData && newNoteData.page === pair[0].id" class="new-note-form" :style="{ top: newNoteData.y + '%', left: newNoteData.x + '%' }">
                <textarea v-model="newNoteData.content" placeholder="Votre note..." class="form-control mb-2"></textarea>
                <input type="file" @change="onFileSelected" class="form-control-file mb-2">
                <button @click.stop="submitNewNote" class="btn btn-sm btn-primary">Enregistrer</button>
                <button @click.stop="cancelNewNote" class="btn btn-sm btn-secondary">Annuler</button>
              </div>
            </template>
            <div class="page-number">{{ pair[0].page_number }}</div>
          </div>
          <div class="page-placeholder" v-else></div>

          <!-- Page de droite -->
          <div class="page right" v-if="pair[1]" @click="startNewNote($event, pair[1])">
            <div class="card-body">
              <img v-if="pair[1].active_image_version && pair[1].active_image_version.fichier_jpeg"
                   :src="getStrapiMediaUrl(pair[1].active_image_version.fichier_jpeg.url)"
                   alt="Version Image"
                   class="version-image">
               <div class="products-grid">
                <div v-for="product in pair[1].products" :key="product.id" class="product-item">
                  <img v-if="product.image"
                       :src="getStrapiMediaUrl(product.image.url)"
                       :alt="product.name"
                       class="product-image">
                  <strong>{{ product.name }}</strong>
                  <span>${{ product.price }}</span>
                  <p>{{ product.description }}</p>
                </div>
              </div>
            </div>
            <template v-if="isNoteMode">
              <div 
                v-for="note in pair[1].notes" 
                :key="note.documentId" 
                class="note-bubble" 
                :class="{ 'is-dragging': draggedNote && draggedNote.note.documentId === note.documentId }"
                :style="{ top: note.y + '%', left: note.x + '%' }"
                @mousedown.stop="startDrag($event, note)"
              >
                <button @click.stop="deleteNote(note)" class="delete-note-btn">×</button>
                <div class="note-content-wrapper">
                  <a v-if="note.attachment" :href="getStrapiMediaUrl(note.attachment.url)" target="_blank" class="paperclip-link">
                    <i class="bi bi-paperclip"></i>
                  </a>
                  <p>{{ note.content }}</p>
                </div>
              </div>
              <div v-if="newNoteData && newNoteData.page === pair[1].id" class="new-note-form" :style="{ top: newNoteData.y + '%', left: newNoteData.x + '%' }">
                <textarea v-model="newNoteData.content" placeholder="Votre note..." class="form-control mb-2"></textarea>
                <input type="file" @change="onFileSelected" class="form-control-file mb-2">
                <button @click.stop="submitNewNote" class="btn btn-sm btn-primary">Enregistrer</button>
                <button @click.stop="cancelNewNote" class="btn btn-sm btn-secondary">Annuler</button>
              </div>
            </template>
            <div class="page-number">{{ pair[1].page_number }}</div>
          </div>
          <div class="page-placeholder" v-else></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import strapiApi from '@/services/strapiApi';

export default {
  name: 'CatalogDetail',
  props: {
    id: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      catalog: null,
      loading: true,
      error: null,
      strapiBaseUrl: process.env.VUE_APP_STRAPI_URL,
      newNoteData: null,
      isNoteMode: false,
      notesLoaded: false,
      draggedNote: null, // For drag and drop
    };
  },
  computed: {
    pairedPages() {
      if (!this.catalog || !this.catalog.pages) {
        return [];
      }
      const pages = [...this.catalog.pages].sort((a, b) => a.page_number - b.page_number);
      const pairs = [];
      if (pages.length > 0) {
        pairs.push([null, pages[0]]);
      }
      for (let i = 1; i < pages.length; i += 2) {
        if (i + 1 < pages.length) {
          pairs.push([pages[i], pages[i + 1]]);
        } else {
          pairs.push([pages[i], null]);
        }
      }
      return pairs;
    },
  },
  async created() {
    this.fetchInitialData();
  },
  methods: {
    getStrapiMediaUrl(path) {
      if (!path) return '';
      return `${this.strapiBaseUrl}${path}`;
    },
    async fetchInitialData() {
      this.loading = true;
      try {
        const response = await strapiApi.getCatalog(this.id);
        this.catalog = response.data.data;
      } catch (err) {
        this.error = 'Erreur lors du chargement du catalogue.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
    async toggleNoteMode() {
      this.isNoteMode = !this.isNoteMode;
      if (this.isNoteMode && !this.notesLoaded) {
        await this.fetchNotes();
      }
    },
    async fetchNotes() {
      this.loading = true;
      try {
        const response = await strapiApi.getCatalogWithNotes(this.id);
        this.catalog = response.data.data;
        this.notesLoaded = true;
      } catch (err) {
        this.error = 'Erreur lors du chargement des notes.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
    startNewNote(event, page) {
      if (!this.isNoteMode || !page) return;
      if (this.newNoteData) return;

      if (event.target.closest('.note-bubble') || event.target.closest('.new-note-form')) {
          return;
      }

      const rect = event.currentTarget.getBoundingClientRect();
      const x = ((event.clientX - rect.left) / rect.width) * 100;
      const y = ((event.clientY - rect.top) / rect.height) * 100;

      this.newNoteData = {
        x: x,
        y: y,
        page: page.id,
        content: '',
        attachment: null,
      };
    },
    onFileSelected(event) {
      const file = event.target.files[0];
      if (file) {
        this.newNoteData.attachment = file;
        const reader = new FileReader();
        reader.readAsDataURL(file);
      }
    },
    async submitNewNote() {
      if (!this.newNoteData || !this.newNoteData.content) {
        alert('Veuillez saisir un contenu pour la note.');
        return;
      }
      try {
        let attachmentId = null;
        if (this.newNoteData.attachment) {
          const response = await strapiApi.upload(this.newNoteData.attachment);
          attachmentId = response.data[0].id;
        }

        const noteData = {
          ...this.newNoteData,
          attachment: attachmentId,
        };

        await strapiApi.createNote(noteData);
        await this.fetchNotes();
        this.newNoteData = null;
      } catch (err) {
        console.error("Erreur lors de l'ajout de la note:", err);
        alert("Erreur lors de l'ajout de la note. Veuillez réessayer.");
      }
    },
    cancelNewNote() {
      this.newNoteData = null;
    },
    async deleteNote(noteToDelete) {
      if (confirm('Êtes-vous sûr de vouloir supprimer cette note ?')) {
        try {
          await strapiApi.deleteNote(noteToDelete.documentId);
          await this.fetchNotes(); // Refresh notes
        } catch (err) {
          console.error('Erreur lors de la suppression de la note:', err);
          alert('Erreur lors de la suppression de la note.');
        }
      }
    },
    startDrag(event, note) {
      if (!this.isNoteMode) return;
      const pageElement = event.target.closest('.page');
      if (!pageElement) return;

      const rect = pageElement.getBoundingClientRect();

      this.draggedNote = {
        note,
        initialX: note.x,
        initialY: note.y,
        initialMouseX: event.clientX,
        initialMouseY: event.clientY,
        pageWidth: rect.width,
        pageHeight: rect.height,
      };

      window.addEventListener('mousemove', this.handleDrag);
      window.addEventListener('mouseup', this.stopDrag);
    },

    handleDrag(event) {
      if (!this.draggedNote) return;
      event.preventDefault();

      const { note, initialX, initialY, initialMouseX, initialMouseY, pageWidth, pageHeight } = this.draggedNote;

      const deltaX = event.clientX - initialMouseX;
      const deltaY = event.clientY - initialMouseY;

      const deltaX_percent = (deltaX / pageWidth) * 100;
      const deltaY_percent = (deltaY / pageHeight) * 100;

      note.x = initialX + deltaX_percent;
      note.y = initialY + deltaY_percent;
    },

    async stopDrag() {
      if (!this.draggedNote) return;
      const { note, initialX, initialY } = this.draggedNote;
      window.removeEventListener('mousemove', this.handleDrag);
      window.removeEventListener('mouseup', this.stopDrag);

      if (note.x !== initialX || note.y !== initialY) {
        try {
          await strapiApi.updateNote(note.documentId, { x: note.x, y: note.y });
        } catch (err) {
          console.error('Erreur lors de la mise à jour de la position de la note:', err);
          alert('Impossible de sauvegarder la nouvelle position de la note.');
          note.x = initialX;
          note.y = initialY;
        }
      }
      this.draggedNote = null;
    },
  },
};
</script>

